import Person from '../../assets/person.png'

const UserCard = ({avatar= Person, first_name = 'First Name', last_name = 'Last Name', email = 'Email', id, onModal}) => {
    return (
        <div class="card mb-5" style={{ width: '18rem' }}>
            <img src={avatar} class="card-img-top" alt="avatar" style={{ width: '100%', height: '300px', objectFit: 'cover' }} />
            <div class="card-body">
                <h5 class="card-title">{first_name} {last_name}</h5>
                <p class="card-text">{email}</p>
            </div>
            <button className="btn btn-primary mb-2" type="submit" onClick={() => onModal(id)}>Personal Info</button>
            <button className="btn btn-warning" type="submit" onClick={() => onModal(id)}>Edit</button>
        </div>
    )
}

export default UserCard