import { BrowserRouter as Router, Switch, Route } from "react-router-dom";

import Login from './pages/Auth/Login'
import Register from './pages/Auth/Register'
import Error from './pages/Error'
import UserList from './pages/UserList'


import Profile from './pages/Profile'
import GuestRoute from './pages/GuestRoute'
import PrivateRoute from './pages/PrivateRoute'

const adminToken = window.localStorage.getItem('adminToken')


function App() {
  return (
    <div className="App">
        <Router>
            <Switch>
                {/* Main Page */}
                <Route exact path='/' component={UserList}/>

                {/* Register Pages */}
                <GuestRoute token={adminToken} path='/login' component={Login}/>
                <GuestRoute token={adminToken} path='/register' component={Register}/>

                {/* Admin Page */}
                <PrivateRoute token={adminToken} path='/profile' component={Profile}/>

                
                <Route path='/private' component={PrivateRoute}/>

                <Route path='*' component={Error}/>
            </Switch>
        </Router>
    </div>
  );
}

export default App;
