import {Link} from 'react-router-dom'
import axios from 'axios'
import {useRef} from 'react'

const Login = () => {

    const email = useRef(null)
    const password = useRef(null)

    // console.log(email, password);

    const formSubmit = (e) =>{
        e.preventDefault()
        // console.log('Hello');
        axios.post(`https://reqres.in/api/login`, {
            email: email.current.value,
            password: password.current.value
          })
          .then(function (response) {
            console.log(response);
            window.localStorage.setItem('adminToken', response.data.token)
            window.location.reload()
          })
          .catch(function (error) {
            console.log(error);
          });
    }

    return (
        <div className="container col-md-6">
            <form className=" mt-5 border border-secondary" style={{borderRadius: '5px'}} onSubmit={formSubmit}>
                <div className="card-header">
                    <h1 className="h1">Login </h1>
                </div>
                <div className="mb-3 px-3">
                    <label htmlFor="exampleInputEmail1" className="form-label">Email address</label>
                    <input ref={email} type="email" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Elektron Pochtangiz..."/>
                    <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                </div>
                <div className="mb-3 px-3">
                    <label htmlFor="exampleInputPassword1" className="form-label">Password</label>
                    <input ref={password} type="password" className="form-control" id="exampleInputPassword1" placeholder="Parolingiz..."/>
                </div>
                <Link to="/register" className="px-3">Parollingiz esingizdan chiqdimi?</Link>
                <div className="card-header p-3">
                    <button type="submit" className="btn btn-primary p-2" style={{width: '100%'}}>Submit</button>
                </div>
            </form>
        </div>
    )
}

export default Login