import {Route, Redirect} from 'react-router-dom';

const PrivateRouter = (props) => {

    const {token} = props;

    return(
        <>
            {
                token ? (
                    <Route {...props}/>
                ) : (
                    <Redirect to="/login"/>
                )
            }
        </>
    )
}

export default PrivateRouter;