import {Route, Redirect} from 'react-router-dom'

const GuestRoute = (props) => {

    const {token} = props
 
    return(
        <>
            {
                token ? (
                    <Redirect to={'/'}/>
                ) : (
                    <Route {...props} to='/login'/>
                )
            }
        </>
    )
}

export default GuestRoute;