import axios from 'axios'
import { useEffect, useState, useRef } from 'react';
import { Link } from 'react-router-dom';
import Login from './../Auth/Login'

import Modal from '../../components/Modal';
import UserCard from '../../components/UserCard';

let adminTooken = window.localStorage.getItem('adminToken')

// console.log(adminTooken);


// Remove Token

const removeToken = () => {
    console.log('Hello');

    window.location.reload()

    adminTooken = window.localStorage.removeItem('adminToken');
}




const UserList = () => {

    const [click, setClick] = useState(false)

    
    const [single, setSingle] = useState({
        isFetched: false,
        data: [],
        error: null
    })
    

    const onModal = (id) =>{
        // e.preventDefault();
        setClick(true)
        // console.log(id);  
        
        axios.get(`https://reqres.in/api/users/${id}`, {
            header: {
                token: adminTooken ? adminTooken : ''
            }
        })
            .then((response) => {
                // console.log(response);
                setSingle({
                    isFetched: true,
                    data: response.data.data,
                    error: false,
                })
            })
            .catch((err) => {
                setSingle({
                    isFetched: true,
                    data: [],
                    error: true,
                })
            })
        
    }


    // User List Main Data...

    const [userlist, setUserList] = useState({
        isFetched: false,
        data: [],
        error: null
    })

    useEffect(() => {
        axios.get('https://reqres.in/api/users?page=1', {
            header: {
                token: adminTooken ? adminTooken : ''
            }
        })
            .then((response) => {
                // console.log(response);
                setUserList({
                    isFetched: true,
                    data: response.data.data,
                    error: false,
                })
            })
            .catch((err) => {
                setUserList({
                    isFetched: true,
                    data: [],
                    error: true,
                })
            })
    }, [])

    // console.log(userlist);



    //  ------------------------------------------------------- New User ---------------------------------- //

    const [newuser, setNewUser] = useState(false)
    const name = useRef(null)
    const email = useRef(null)
    const lastname = useRef(null)
    const [createuser, setCreateUser] = useState({
        isFetched: false,
        data: [],
        error: null
    })

    const newuserdata = () =>{
        setNewUser(true)
    }

    const newuserCreate = (e) =>{

        e.preventDefault()

        axios.post('https://reqres.in/api/users', {
            first_name: name.current.value,
            email: email.current.value,
            last_name: lastname.current.value,
            header: {
                token: adminTooken ? adminTooken : ''
            }
        })
            .then((response) => {
                console.log(response);
                setCreateUser({
                    isFetched: true,
                    data: response,
                    error: false,
                })
                setNewUser(false)
        
                name.current.value = ''
                email.current.value = ''
                lastname.current.value = ''


                setUserList((prevData) =>{
                    return {
                        ...prevData,
                        data: [
                            ...prevData.data,
                            {
                                last_name: response.data.last_name,
                                first_name: response.data.first_name,
                                email: response.data.email,
                                avatar: 'https://wallpaperaccess.com/full/3909258.jpg',
                            }    
                        ]
                    }
                })


            })
            .catch((err) => {
                setCreateUser({
                    isFetched: true,
                    data: [],
                    error: true,
                })

            })
    }

    console.log('pastegi');
    console.log(createuser);
    console.log(setUserList);



    return (
        <div>
            
            <div style={{ backgroundColor: 'blue' }} className="p-3 mb-3 d-flex justify-content-beetwen align-item-center">
                <h1 className="container" style={{ color: 'white' }}>UserList Page</h1>
                {
                    adminTooken ? (
                        <button type="button" className="btn btn-warning" onClick={() => removeToken()}>Log out</button>
                    ) : (
                        <Link to='/login' className="btn btn-warning d-inline-block px-2">Login</Link>
                    )
                }
            </div>

            {
                adminTooken ? (
                    <div className="container mb-4">
                        <button className="btn btn-success" style={{width: '100%'}} onClick={newuserdata} >Add New User</button>
                    </div>
                ) : (
                    <></> 
                )
            }

            {/* New User */}

            <Modal title="New User..." onclick={newuser} close={setNewUser} >
                <form className="" style={{borderRadius: '5px'}} onSubmit={newuserCreate} >
                
                    <div className="mb-3 px-3">
                        <label htmlFor="exampleInputEmail1" className="form-label">New User Name</label>
                        <input ref={name} type="text" className="form-control" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Full Name..."/>
                        <div id="emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                    </div>
                    <div className="mb-3 px-3">
                        <label htmlFor="exampleInputPassword1" className="form-label">New User LastName</label>
                        <input ref={lastname} type="text" className="form-control" id="exampleInputPassword1" placeholder="LastName..."/>
                    </div>
                    <div className="mb-3 px-3">
                        <label htmlFor="exampleInputPassword1" className="form-label">New User Email</label>
                        <input ref={email} type="email" className="form-control" id="exampleInputPassword1" placeholder="User Email..."/>
                    </div>
                    <div className="p-3">
                        <button type="submit" className="btn btn-primary p-2" style={{width: '100%'}} >Submit</button>
                    </div>
                </form>
            </Modal>


            <div className="container d-flex justify-content-between flex-wrap">

                {
                    userlist.data.map((el) => {
                        // console.log(el.first_name);
                        return (
                            <UserCard
                                avatar={el.avatar}
                                first_name={el.first_name}
                                last_name={el.last_name}
                                email={el.email}
                                id={el.id}
                                onModal={onModal}
                            />
                        )
                    })
                }
            </div>

            <Modal title="User Info" onclick={click} close={setClick} >
                <h3>User ID: {single.data.id}</h3>
                <h3>User Name: {single.data.first_name}</h3>
                <h3>User LastName: {single.data.last_name}</h3>
                <h3>User Email: {single.data.email}</h3>
            </Modal>

        </div>
    )
}

export default UserList;